* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-image: url("img.3072d738.jpg");
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  width: 100%;
  display: block;
}

.conteiner__x {
  margin: 100px 50px 50px;
  display: flex;
}

.content {
  width: 156px;
  margin: 0 auto;
}

.item {
  width: 50px;
  height: 50px;
  text-align: center;
  cursor: pointer;
  vertical-align: middle;
  border: 1px solid #000;
  border-radius: 4px;
  font-size: 25px;
  line-height: 50px;
  transition: background .1s;
  display: inline-block;
}

.item:hover {
  background-color: rgba(241, 241, 241, .047);
}

.box {
  width: 100px;
  height: 100px;
  background-color: red;
}

.conteiner {
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

.js-conteiner {
  width: 200px;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.box__item {
  width: 50px;
  height: 50px;
  text-align: center;
  background-color: #8d8d94;
  border: 2px solid #000;
  border-radius: 4px;
  line-height: 50px;
  display: block;
  box-shadow: inset 0 0 50px rgba(0, 0, 0, .5);
}

.btn {
  width: 200px;
  height: 50px;
  background-color: #8d8d94;
  border-radius: 4px;
  margin: 30px;
  box-shadow: inset 0 0 50px rgba(0, 0, 0, .5);
}

